.header {
  position: fixed;
  top: 0;
  width: 100%;
  transition: top 0.3s;
  z-index: 999 !important;
}
.header-container {
  display: flex;
  flex-direction: column;
}
._31vol {
  height: 3rem;
  border-left: 1px solid #fff;
  margin: 0 2.5rem;
  opacity: 0.3;
}
.head-bottom {
  display: flex !important;
}
.mobile-top-nav {
  height: 42px;
  align-items: center;
  white-space: nowrap;
  list-style-type: none;
  display: flex;
  background-color: #fff;
}

@media (min-width: 1025px) {
  .header-container {
    margin-bottom: 4rem;
  }
  .head-top {
    background-color: #35144f;
    height: 80px;
    box-shadow: 0 1px 2px 0 rgb(0 0 0 / 15%);
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
  }
  .XRy60 {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  ._3ay05 {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  ._1atAn {
    font-size: 20px;
    letter-spacing: 0.4px;
    margin-right: 3.5rem;
    line-height: 1.38;
    padding: 1rem 3.5rem;
    font-weight: 700;
    color: #35144f;
    background-color: #febf04;
    border-radius: 30px;
  }
  ._1R7OS {
    font-size: 20px;
    letter-spacing: 0.4px;
    margin-right: 3.5rem;
    padding: 1rem 2rem;
    color: rgb(41, 41, 41);
    border-radius: 30px;
    border: 1px solid #b1b1b1;
    background-color: #efefef;
    margin-left: 1rem;
    font-weight: 700;
    line-height: 1.3;
  }
  .head-bottom {
    box-shadow: 0 1px 2px 0 rgb(0 0 0 / 15%);
    overflow: unset;
    justify-content: center;
  }
  .J_MHx {
    font-size: 18px;
    line-height: 1.33;
    color: #858585;
    font-weight: 500;
    padding-right: 2rem;
  }
  .J_MHx a {
    color: #858585;
  }
  .IZNLQ {
    display: none;
  }
}
