.footer {
  background-color: #35144f;
  width: 100%;
}
.footer-container {
  margin: 0 auto;
  overflow-wrap: break-word;
  word-break: break-word;
  display: block;
}
.sec-header {
  color: #febf04;
  font-size: 16px;
  font-weight: 700;
}
.section-list {
  list-style: none;
  overflow: hidden;
}
li {
  list-style: none;
}
._1h-7a {
  color: #fff;
  float: left;
}
._1h-7a a {
  color: #fff;
}
.footer-section {
  overflow-wrap: break-word;
  word-break: break-word;
}
.trending-section {
  border-bottom: 1px solid #bababa;
}
.follow-us {
  color: #febf04;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.43;
}
.footer-grid {
  display: inline-grid;
}
@media (min-width: 1025px) {
  .footer-container {
    max-width: 1110px;
    padding: 0;
  }
  .footer-section {
    padding: 4rem 0;
    height: auto;
  }
  .sec-header {
    margin-bottom: 2rem;
  }
  ._1h-7a {
    font-size: 16px;
    padding: 0 3rem 1.5rem 0;
    line-height: 1.38;
  }
  .trending-section {
    padding-bottom: 2.5rem;
    margin: 2.5rem 0 4rem;
  }
  .footer-last {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 95%;
  }
  .menu-item-link {
    color: #fff;
    float: left;
    font-size: 14px;
    line-height: 1.38;
    padding: 0 3rem 1.5rem 0;
  }
}
