@media (min-width: 1025px) {
  .second-group-collection {
    max-width: 1110px;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .entertainment-grid {
    display: grid;
    grid-template-columns: 6fr 6fr;
    column-gap: 3.5rem;
    margin-bottom: 6rem;
  }
  .headline-type--6 {
    font-size: 36px;
  }
  .card-image-headline {
    position: absolute;
    opacity: 0.85;
    background-color: #fff;
    padding: 1rem;
    bottom: 35px;
    width: 78%;
    border-radius: 0 5px 5px 0;
  }
  .headline-type--4 {
    color: #292929;
    font-weight: 700;
    line-height: 1.38;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
}
.grid-flex {
  display: flex;
  flex-direction: column;
}
.heading0 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.headline-type--4 {
  color: #292929;
  line-height: 1.17;
  margin-top: 1rem;
  font-size: 16px;
  font-weight: 700;
  -webkit-line-clamp: 1;
  overflow: hidden;
  display: flex;
  flex-direction: row;
}
.card-image--wrapper {
  position: relative;
}
.qt-image {
  position: relative;
  background-color: #f7f7f7;
  background-size: cover;
  background-position: 50%;
}
.qt-image img {
  /* position: absolute; */
  image-rendering: auto !important;
  border-radius: 5px;
  width: 100%;
  height: 100%;
  max-width: 100%;
  object-fit: cover;
}
.row_1-grid {
  display: grid;
  grid-template-columns: 6fr 6fr;
  column-gap: 3.5rem;
  margin-top: 4.5rem;
}
.small-story-card {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  border-top: 0.5rem solid #35144f;
  border-radius: 5px;
  box-shadow: 0 1px 2px 0 rgb(0 0 0 / 15%);
  position: relative;
}
.title--wrapper {
  padding: 0 1.5rem 1rem;
  min-height: 112px;
}
.headline-type--6 {
  color: #292929;
  margin-top: 1rem;
  line-height: 1.38;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  font-size: 36px;
  font-weight: 700;
  letter-spacing: 400;
}
