.story-head {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #bababa;
  margin-bottom: 3rem;
}
.headline-type-20 {
  text-align: left;
  margin-bottom: 2rem;
  color: #35144f;
  font-weight: 800;
  letter-spacing: 400;
}
.para {
  color: #6c6c6c;
  text-align: left;
}
.section-timestamp {
  margin-bottom: 2rem;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
.sec-head {
  font-size: 14px;
  font-weight: 800;
  line-height: 1.43;
  color: #35144f;
  text-transform: uppercase;
}
.sec-update {
  font-size: 14px;
  line-height: 1.43;
  color: #6c6c6c;
}
.sec-update time {
  font-weight: 700;
}
.news_type {
  font-size: 14px;
  font-weight: 700;
  line-height: 1.43;
  color: #35144f;
  text-transform: uppercase;
}
@media screen and (min-width: 768px) {
  .headline-type-20 {
    font-size: 36px;
  }
  .para {
    line-height: 1.3;
    font-size: 20px;
    margin-bottom: 2rem;
  }
  .subs {
    display: block;
    margin-bottom: 4rem;
  }
  .subs-head {
    display: flex;
    flex-direction: row;
    align-items: cente;
  }
  .subs-head-text {
    position: relative;
    font-size: 25px;
    font-weight: 800;
    line-height: 1.23;
    color: #35144f;
    margin-bottom: 1rem;
    padding-right: 1rem;
  }
  .line {
    height: 5px;
    background-color: #febf04;
    flex: 1;
    margin-top: 10px;
    border-radius: 5px;
  }
  .social-share-links {
    display: grid;
    grid-template-columns: repeat(6, auto);
    column-gap: 2rem;
    list-style-type: none;
    margin-bottom: 3rem;
  }
  .social-share-links li {
    flex: 1;
  }
  .facebook {
    background-color: #1877f2;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 2px 1rem;
    border-radius: 5px;
    cursor: pointer;
    height: 38px;
  }
  .twitter {
    background-color: #1da1f2;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 2px 1rem;
    border-radius: 5px;
    cursor: pointer;
    height: 38px;
  }
  .whatsapp {
    background-color: #25d366;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 2px 1rem;
    border-radius: 5px;
    cursor: pointer;
    height: 38px;
  }
  .telegram {
    background-color: #33a0cd;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 2px 1rem;
    border-radius: 5px;
    cursor: pointer;
    height: 38px;
  }
  .messanger {
    background-color: #448aff;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 2px 1rem;
    border-radius: 5px;
    cursor: pointer;
    height: 38px;
  }
  .comment-wrapper {
    background-color: #858585;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 2px 1rem;
    border-radius: 5px;
    cursor: pointer;
    height: 38px;
  }
  .icon-title {
    font-weight: 600;
    color: #fff;
    margin: 0 auto;
    display: block;
    min-width: 40px;
  }
  .title {
    font-weight: 600;
    line-height: 1.43;
    color: #fff;
    word-break: keep-all;
    margin: 0 auto;
    min-width: 40px;
  }
  .story-element-text p {
    font-size: 20px;
    line-height: 1.6;
    margin-bottom: 3rem;
  }
  .story-element-text-blurb {
    margin-top: 4rem;
  }
  .story-element-text-blurb blockquote {
    font-size: 22px;
    line-height: 1.55;
    color: #35144f;
    font-weight: 700;
  }
  .subs-para {
    font-size: 16px;
    font-weight: 500;
    line-height: 1.38;
    color: #292929;
    margin-bottom: 2rem;
  }
  .subs-form {
    margin-bottom: 1rem;
    display: flex;
    flex-direction: row;
    width: 100%;
  }
  .inp-sec {
    position: relative;
    width: 100%;
  }
  .inp {
    height: 100%;
    width: 100%;
    font-size: 16px;
    border-radius: 5px 0 0 5px;
    border: 1px solid #bababa;
  }
  .subs-btn {
    /* line-height: 1.38; */
    padding: 1.5rem;
    text-align: center;
    color: #fff;
    background-color: #35144f;
    border: #35144f;
    border-radius: 0 5px 5px 0;
    min-width: 105px;
  }
  .subscribers {
    font-size: 16px;
    font-weight: 500;
    line-height: 1.38;
    color: #bababa;
  }
  .story-element-head h2 {
    font-size: 22px;
    line-height: 1.27;
    font-weight: 700;
    color: #292929;
  }
  ._1a5aS {
    margin-bottom: 4rem;
  }
  ._1Jj4B {
    margin-bottom: 1rem;
    font-size: 22px;
    line-height: 1.27;
    font-weight: 800;
    color: #35144f;
  }
  .headline-type--17 {
    flex: 10;
    margin-right: 3rem;
    font-size: 18px;
  }
  .story-element-text-quote {
    padding-left: 2rem;
    margin-bottom: 4rem;
    border-left: 0.5rem solid #35144f;
    font-weight: 700;
    color: #292929;
  }
  .story-element-text-quote blockquote {
    font-size: 20px;
    line-height: 1.6;
    margin-bottom: 1rem;
    color: #35144f;
    font-weight: bold;
  }
}
.story-body-wrapper {
  display: block;
  overflow: hidden;
  margin: 0 -2rem;
  padding: 0 2rem;
  position: relative;
  max-height: 700px;
}
.story-element-text {
  color: #292929;
}
.story-element-text-blurb {
  border-bottom: 1px solid #bababa;
  border-top: 1px solid #bababa;
  padding: 3rem 0;
  color: #292929;
}
.subs-para {
  font-size: 16px;
  font-weight: 500;
  line-height: 1.38;
  color: #292929;
  margin-bottom: 2rem;
}
.story-element-head {
  margin-bottom: 2rem;
}
._2FuPS {
  display: flex;
  flex-direction: row;
  border-top: 1px solid #bababa;
  border-bottom: 1px solid #bababa;
  padding: 2.5rem 0;
  align-items: center;
}
